import React, { Component } from "react";
import axios from "axios";
import DocTypeComponent from "./DocTypeComponent";
import { Link } from "react-router-dom";
import NavigationForAdmin from "../NavigationForAdmin";
import ApiUrl from "../../APIURL";

class DocTypeList extends Component {
  constructor() {
    super();
    this.state = {
      doctypes: [],
      currentPage: 1,
      doctypesPerPage: 10
    };
  }

  componentDidMount() {
    this.getDoctypes();
  }

  getDoctypes = () => {
    axios
      .get(`${ApiUrl}doctypes`)
      .then(response => {
        this.setState({ doctypes: response.data });
      })
      .catch(error => {
        alert("Nra galimybs pateikti duomen apie dokument tipus.");
      });
  };

  handlePageChange = (event) => {
    this.setState({
      currentPage: Number(event.target.id)
    });
  }

  render() {
    const { currentPage, doctypesPerPage } = this.state;
    const indexOfLastDoctype = currentPage * doctypesPerPage;
    const indexOfFirstDoctype = indexOfLastDoctype - doctypesPerPage;
    const currentDoctype = this.state.doctypes.slice(indexOfFirstDoctype, indexOfLastDoctype);
    let doctype = currentDoctype.map((doctype, index) => {
      return (
        <DocTypeComponent
          key={index}
          index={index}
          id={doctype.id}
          title={doctype.title}
        />
      );
    });
    const pageNumbers = [];
    for (let i = 1; i <= Math.ceil(this.state.doctypes.length / doctypesPerPage); i++) {
      pageNumbers.push(i);
    }
    const renderPageNumbers = pageNumbers.map((number, index) => {
      if (
        (number === this.state.currentPage - 1 || number === this.state.currentPage || number === this.state.currentPage + 1)
      ) {
        return (
          <button className="btn btn-dark" key={index} id={number} onClick={this.handlePageChange}>{number}</button>
        )
      } else {
        return (
          <div key={index}></div>
        );
      }
    });
    return (
      <div>
        <div className="container-fluid">
          <div className="row">
            <div className="col-12">
              <NavigationForAdmin />
            </div>
          </div>
        </div>
        <div className="container ">
          <Link to="/Gentoo/admin/doctypes/add">
            <button
              type="button"
              className="btn btn-dark my-4"
              onClick={this.props.onAddNew}
            >
              Pridti nauj dokument tip
            </button>
          </Link>
          <table className=" text-center table table-striped">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Dokument tipo pavadinimas</th>
                <th scope="col" colSpan="2">
                  Veiksmai su dokument tipais
                </th>
              </tr>
            </thead>
            <tbody>{doctype}</tbody>
          </table>
          <nav aria-label="Page navigation example">
            <ul className="pagination justify-content-center">
              <li className="page-item">
                {renderPageNumbers}
              </li>
            </ul>
          </nav>
        </div>
      </div>
    );
  }
}

export default DocTypeList;
