import React, { Component } from "react";
import axios from "axios";
import EditDoctypeFormComponent from "./EditDoctypeFormComponent";
import ApiUrl from "../../APIURL";

class EditDoctypeFormContainer extends Component {
  constructor() {
    super();
    this.state = {
      title: ""
    };
  }

  componentDidMount() {
    this.getDoctype();
  }

  getDoctype = () => {
    axios
      .get(`${ApiUrl}doctypes/${this.props.match.params.doctypeid}`)
      .then(response => {
        this.setState(response.data);
      })
      .catch(error => {
        alert("Tokio dokument tipo nra.");
      });
  };

  onChange = e => {
    const state = this.state;
    state[e.target.name] = e.target.value;
    this.setState(state);
  };

  onSubmit = event => {
    event.preventDefault();
    axios
      .put(
        `${ApiUrl}doctypes/
          ${this.props.match.params.doctypeid}`,
        {
          title: this.state.title
        }
      )
      .then(() => {
        alert("Dokument tipo duomenys atnaujinti skmingai.");
        this.props.history.push("/Gentoo/admin/doctypes");
      })
      .catch(function(error) {
        alert(
          "Dokument tipo duomen isaugoti nepavyko. Bandykite dar kart."
        );
      });
  };

  onBack = event => {
    event.preventDefault();
    this.props.history.push(`/Gentoo/admin/doctypes`);
  };

  render() {
    return (
      <div>
        <EditDoctypeFormComponent
          title={this.state.title}
          onBack={this.onBack}
          onSubmit={this.onSubmit}
          onChange={this.onChange}
        />
      </div>
    );
  }
}

export default EditDoctypeFormContainer;
